import { navigate } from 'gatsby-link'
import React, { useEffect } from 'react'
import { Helmet } from 'react-helmet'

const Share = ({ pageContext }) => {
  useEffect(() => navigate('/'))
  return (
    <Helmet>
      <meta property="og:url" content={pageContext.url} />
      <meta property="og:image" content={pageContext.image} />
      <meta property="og:image:width" content="1200" />
      <meta property="og:image:height" content="630" />
      <meta property="og:title" content="然後，錢包就死了" />
      <meta property="og:description" content={pageContext.description} />
      <title>然後，錢包就死了</title>
    </Helmet>
  )
}

export default Share
